.main {
  height: 100%;
  display: grid;
  grid-template-columns: 5% auto 5%;
  grid-auto-rows: minmax(100px, 100%);
  grid-template-areas: ". main .";
}

.content {
  grid-area: main;
  display: grid;
  grid-template-rows: 10fr 50px;
  grid-auto-columns: 1fr 10fr 1fr;
  grid-gap: 10px;
  grid-template-areas:
    ". show ."
    ". send .";
}

.show {
  grid-area: show;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-auto-columns: auto;

  grid-template-areas:
    "."
    "entries";
  overflow-y: scroll;
}

/* width */
.show::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.show::-webkit-scrollbar-track {
  background: black;
}

/* Handle */
.show::-webkit-scrollbar-thumb {
  background: red;
}

.entries {
  grid-area: entries;
  max-width: 70vw;
}

.entry {
  display: block;
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 400;
  color: green;
  padding: 0;
  margin: 0;
}

.message {
  text-align: justify;
  word-wrap: break-word;
}

.send {
  grid-area: send;

  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
  padding-bottom: 1%;
}

input {
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 400;
  color: green;
  background-color: black;
}
